import React, { PureComponent } from "react";
import ReactApexChart from "react-apexcharts";
import ApexCharts from "apexcharts";
import moment from "moment";

export default class AssetsTypeLineChart extends PureComponent {
  constructor(props) {
    super(props);
    this.chartRef = React.createRef(); // Create a reference to the chart
 
    this.state = {
      series: this.props.contentAnalyticsData.newSeries,
      options: {
        colors: props.section==1 ? ["#546FED", "#EF45B1"] : ["#546FED", "#F8B861", "#EF45B1"],
        chart: {
          id: "analyticsOverview",
          type: "line",
          events: {
            mounted: () =>  {
              const chart = this.chartRef.current.chart; // Access the chart instance
              if(props.section==1){
                chart.hideSeries("Average Recommendation Score"); // Hide the second series by default
              }
            },
            legendClick: (chartContext, seriesIndex, config) => {
                const chart = this.chartRef.current.chart; // Access the chart instance
                const allSeries = chart.w.globals.series.slice();

                allSeries.forEach((_, index) => {
                  if (index !== seriesIndex) {
                    chartContext.hideSeries(
                      chartContext.w.globals.seriesNames[index]
                    );
                  } else {
                    chartContext.showSeries(
                      chartContext.w.globals.seriesNames[index]
                    );
                  }
                });
            },
          },
          animations: {
            enabled: true,
            easing: "linear",
            dynamicAnimation: {
              speed: 1000,
            },
          },
          height: 350,
          zoom: { enabled: false },
          toolbar: { show: false },
        },
        legend: {
          show: true,
          position: "top",
          itemMargin: {
            horizontal: 20,
            vertical: 0,
          },
          onItemClick: {
            toggleDataSeries: false
          },
          onItemHover: {
            highlightDataSeries: true // This controls the hover behavior; set to true/false based on preference
          }
        },
        dataLabels: { enabled: false },
        markers: { size: 0, style: "hollow" },
        stroke: {
          width: [4, 4, 4],
          curve: "smooth",
          lineCap: 'round',  // Makes the line edges rounded
        },
        xaxis: {
          type: "datetime",
          min: this.props.contentAnalyticsData?.unixDate[0],
          tickAmount: 6,
          labels: { 
            rotate: -45,  // Rotate labels to avoid overlap
            formatter: (value) => {
                return moment(value).subtract(1, 'days').format('DD MMM')  // Format as "Month Day"
            }
          } ,
          tooltip: {
            shared: true,
            intersect: true, // Allows tooltip to show when hovering near the points
            formatter: function (value) {
              return moment(value).subtract(1, 'days').format('D-MM-YYYY') // Format as "Month Day"
            },
          },
        },
        yaxis: {
          labels: {
            formatter: function (value) {
              return  value // Rounds the value to the nearest integer
            }
          }
        },
        grid: {
          show: true, // Enable grid
          borderColor: '#E8E8E8', // Grid line color
          strokeDashArray: 0, // Solid lines, or dashed lines by increasing the number
          position: 'back', // 'front' for grid lines in front of the chart
          xaxis: {
            lines: {
              show: true, // Show vertical lines
            },
          },
          yaxis: {
            lines: {
              show: true, // Show horizontal lines
            },
          },
        },
      },
      responsive: [
        {
          // breakpoint: 991,
          options: {
            chart: {
              height: 300,
            },
          },
          // breakpoint: 767,
          options: {
            chart: {
              height: 280,
            },
          },
        },
      ],
    };
  }

  render() {
    const { contentAnalyticsData } = this.props;

    const lastDate =
      contentAnalyticsData?.unixDate[
        contentAnalyticsData?.unixDate?.length - 1
      ];
    if (lastDate) {
      const chart = ApexCharts.getChartByID("area-datetime2");
      if (chart) {
        chart.zoomX(
          contentAnalyticsData?.unixDate[0],
          contentAnalyticsData?.unixDate[lastDate - 1]
        );
      }
    }
   

    return (
      <ReactApexChart
        ref={this.chartRef}
        options={this.state.options}
        series={this.state.series}
        type="line"
        height={350}
      />
    );
  }
}
